import TODO_CONFIG from '../config';
import PARSER_TEST from './parser_test';

const {RENDER_STRING_KEY, RENDER_TAGS_KEY, RENDER_TIME_KEY} = TODO_CONFIG;

const TOKEN_TYPE = {
    TIME: 'time',
    TAG: 'tag',
    TEXT: 'text',
    // MD
    HYPERLINK: 'hyperlink',
    CODE: 'code',
    ITALIC: 'italic',
    EM: 'em',
    EM_ITALIC: 'em-italic',
    DELETE: 'delete',
};

export {TOKEN_TYPE};

const SPECIAL_SYMBOL = {
    LEFT_QUOTE: '[',
    RIGHT_QUOTE: ']',
    TIME: '>',
    COMMA: ',',
    LEFT_SMALL_QUOTE: '(',
    RIGHT_SMALL_QUOTE: ')',
    GRAVE_ACCENT: '`',
    STAR: '*',
    WAVE: '~',
};
const pauseSymbol = ['[', ']', '>', '`', '*', '~'];

/**
 *  
 *   + 
 *  eg: >2d 15:13 [tag1, tag2] your plan
 *
 *  
 *   - >2d 15:30 > 
 *      - >2d: 
 *      - >ed: 
 *      - >ew-3: 
 *   - [xxx, xxx, xxx]:tag
 *
 *  MD
 *   - **XXX** // 
 *   - [xxx](http://www.baidu.com) // 
 *    - `xxx` // 
 *   - ~~xxx~~ // 
 *   - *xxx* // italic
 *   - **xxx** // 
 *   - ***xxx*** //  & italic
 *
 * 
 * result: {
 *     startIndex: xxx,
 *     endIndex: xxx,
 *     key: xxx, // 
 *     content: xxx,
 *     data: {
 *         value: ''
 *     }
 * }
 */

const parser = {
    // Todo
    parse(txt, closeTips) {
        if (typeof txt !== 'string' || txt.length === 0) {
            if (closeTips !== true) {
                console.log('');
            }
            return false;
        }
        txt = txt.trim();
        let length = txt.length;
        let offset = 0;
        let collection = [];
        let startTextIndex = -1;
        // 
        while (offset < length) {
            const result = parser.analyze(txt, offset);
            if (result) {
                let nextOffset = result.end + 1;
                if (nextOffset <= offset) {
                    offset = offset + 1;
                    if (closeTips !== true) {
                        console.log(`: ${txt}, ${offset}`)
                    }
                } else {
                    // 
                    if (startTextIndex !== -1) {
                        let textResult = parser.getText(txt, startTextIndex, offset - 1);
                        collection.push(textResult);
                        startTextIndex = -1;
                    }
                    offset = nextOffset;
                }
                collection.push(result);
            } else {
                if (startTextIndex === -1) {
                    startTextIndex = offset;
                }
                offset = offset + 1;
            }
        }
        // 
        if (startTextIndex !== -1) {
            let textResult = parser.getText(txt, startTextIndex);
            collection.push(textResult);
        }

        // 
        const summaryInfo = explain.getSummaryInfo(collection);
        collection[0] = Object.assign(collection[0], summaryInfo);

        if(closeTips !== true) {
            // console.log(`: ${txt}`);
            console.log(`txt: ${txt}, parseData:`, collection);
        }
        return collection;
    },
    analyze(txt, offset) {
        let symbol = txt[offset];
        let result = null;

        let isFind = pauseSymbol.indexOf(symbol) !== -1;
        if (isFind) {
            switch (symbol) {
                case SPECIAL_SYMBOL.LEFT_QUOTE: {
                    result = parser.quoteParse(txt, offset);
                    break;
                }
                case SPECIAL_SYMBOL.TIME: {
                    result = parser.timeParse(txt, offset);
                    break;
                }
                case SPECIAL_SYMBOL.GRAVE_ACCENT: {
                    result = parser.codeParse(txt, offset);
                    break;
                }
                case SPECIAL_SYMBOL.STAR: {
                    result = parser.emParse(txt, offset);
                    break;
                }
                case SPECIAL_SYMBOL.WAVE: {
                    result = parser.delLineParse(txt, offset);
                    break;
                }
                default: {
                    // do nothing
                }
            }
        }

        return result;
    },
    getText(txt, offset, nextOffset) {
        if (typeof nextOffset !== 'undefined') {
            const content = txt.substring(offset, nextOffset + 1);
            return {
                begin: offset,
                end: nextOffset,
                key: TOKEN_TYPE.TEXT,
                content,
                data: {
                    value: content
                }
            }
        } else {
            const content = txt.substr(offset);
            return {
                begin: offset,
                end: txt.length - 1,
                key: TOKEN_TYPE.TEXT,
                content,
                data: {
                    value: content
                }
            }
        }
    },
    // 
    delLineParse(txt, offset) {
        txt = txt.substr(offset);
        if (txt[1] !== SPECIAL_SYMBOL.WAVE) {
            return null;
        }
        let end = txt.indexOf('~~', 3);
        if (end === -1) {
            return null;
        }
        return {
            begin: offset,
            end: end + offset + 1,
            key: TOKEN_TYPE.DELETE,
            content: txt.substring(0, end + 2),
            data: {
                value: txt.substring(2, end),
            }
        }
    },
    // 
    emParse(txt, offset) {
        txt = txt.substr(offset);
        let end = 0;
        let key = null;
        let content = null;
        let value = null;

        // 
        let expectLevel = 0;
        if (txt[1] === SPECIAL_SYMBOL.STAR) {
            if (txt[2] === SPECIAL_SYMBOL.STAR) {
                expectLevel = 2;
            } else {
                expectLevel = 1;
            }
        }
        // 
        let realLevel = -1;

        // 
        switch (expectLevel) {
            case 0: {
                end = txt.indexOf('*', 2);
                if (end !== -1) {
                    realLevel = 0;
                }
                break;
            }
            case 1: {
                end = txt.indexOf('**', 3);
                if (end === -1) {
                    end = txt.indexOf('*', 3);
                    if (end !== -1) {
                        realLevel = 0;
                    }
                } else {
                    realLevel = 1;
                }
                break;
            }
            case 2: {
                end = txt.indexOf('***', 4);
                if (end === -1) {
                    end = txt.indexOf('**', 4);
                    if (end === -1) {
                        end = txt.indexOf('*', 4);
                        if (end !== -1) {
                            realLevel = 0;
                        }
                    } else {
                        realLevel = 1;
                    }
                } else {
                    realLevel = 2;
                }
                break;
            }
            default: // do nothing
        }

        if (realLevel === -1) {
            return null;
        }
        // 
        switch (realLevel) {
            case 0: {
                key = TOKEN_TYPE.ITALIC;
                value = txt.substring(1, end);
                content = txt.substring(0, end + 1);
                break;
            }
            case 1: {
                key = TOKEN_TYPE.EM;
                value = txt.substring(2, end);
                end = end + 1;
                content = txt.substring(0, end + 1);
                break;
            }
            case 2: {
                key = TOKEN_TYPE.EM_ITALIC;
                value = txt.substring(3, end);
                end = end + 2;
                content = txt.substring(0, end + 1);
                break;
            }
            default: // do nothing
        }
        return {
            begin: offset,
            end: end + offset,
            key,
            content,
            data: {value}
        }
    },
    // 
    codeParse(txt, offset) {
        txt = txt.substr(offset);
        let end = 0;

        end = txt.indexOf(SPECIAL_SYMBOL.GRAVE_ACCENT, 2);

        if (end === -1) {
            return null;
        } else {
            return {
                begin: offset,
                end: end + offset,
                key: TOKEN_TYPE.CODE,
                content: txt.substring(0, end + 1),
                data: {
                    value: txt.substring(1, end)
                }
            }
        }
    },
    quoteParse(txt, offset) {
        txt = txt.substr(offset);
        let end = 0;
        let content = null;
        let data = null;
        let key = TOKEN_TYPE.TAG;

        end = txt.indexOf(SPECIAL_SYMBOL.RIGHT_QUOTE);

        if (end === -1) {
            return null
        }

        content = txt.substring(0, end + 1);
        let preData = txt.substring(1, end);
        // 
        if (txt[end + 1] === SPECIAL_SYMBOL.LEFT_SMALL_QUOTE) {
            let endSmall = txt.indexOf(SPECIAL_SYMBOL.RIGHT_SMALL_QUOTE);
            if (endSmall !== -1) {
                content = txt.substring(0, endSmall + 1);
                key = TOKEN_TYPE.HYPERLINK;
                data = {
                    value: txt.substring(1, end),
                    link: txt.substring(end + 2, endSmall),
                };
                end = endSmall;
            }
            return {
                begin: offset,
                end: end + offset,
                key,
                content,
                data
            }
        } else {
            // 
            let tags = preData.split(SPECIAL_SYMBOL.COMMA);
            tags = tags.map((eachData) => {
                return eachData.trim();
            });
            return {
                begin: offset,
                end: end + offset,
                key,
                content,
                data: {
                    tags,
                    value: tags.join('')
                }
            }
        }
    },
    // 
    timeParse(txt, offset) {
        txt = txt.substr(offset);
        let firIndex = txt.indexOf(' ');
        let secIndex = txt.indexOf(' ', firIndex + 1);
        let delay = txt.substring(1, firIndex); // 

        if (secIndex !== -1) {
            let clock = txt.substring(firIndex + 1, secIndex); // 
            let timeStamp = explain.getTimeStamp(delay, clock);
            return {
                begin: offset,
                end: secIndex + offset,
                content: txt.substring(0, secIndex),
                key: TOKEN_TYPE.TIME,
                data: {
                    delay,
                    clock,
                    timeStamp,
                    value: clock
                }
            }
        } else {
            // >
            if (txt[1] === ' ') {
                return;
            }
            let timeStamp = explain.getTimeStamp(delay);
            return {
                begin: offset,
                end: firIndex + offset,
                content: txt.substring(0, firIndex),
                key: TOKEN_TYPE.TIME,
                data: {
                    delay,
                    timeStamp,
                }
            }
        }

    },
    test() {
        PARSER_TEST.forEach(function (eachData) {
            parser.parse(eachData);
        });
    }
};

const TIME_UNIT = {
    MINUTE: 'm',
    HOURS: 'h',
    DAY: 'd',
    SECOND: 's',
};

const TIME_UNIT_ARRAY = Object.values(TIME_UNIT);

const explain = {
    getTimeStamp(delay, clock) {
        const now = new Date();
        const year = now.getFullYear();
        const month = now.getMonth();
        let date = now.getDate();
        let hour = now.getHours();
        let minutes = now.getMinutes();
        let second = now.getSeconds();

        //  >14:30
        if (delay.indexOf(':') !== -1) {
            clock = delay;
            delay = null;
        }

        // delay
        if (delay) {
            let unit = null;
            for(let i = 0; i<delay.length; i++) {
                let tempChar = delay[i];
                if(Number.isNaN(parseInt(tempChar))) {
                    unit = tempChar;
                    i = delay.length;
                }
            }
            let value = null;
            if (TIME_UNIT_ARRAY.indexOf(unit) === -1) {
                let day = explain.getChineseDay(delay);
                if (Number.isInteger(day)) {
                    let currDay = now.getDay();
                    let diffDay = day - currDay;
                    diffDay = diffDay < 0 ? diffDay + 7 : diffDay;
                    date += diffDay;
                } else {
                    unit = TIME_UNIT.DAY;
                    value = delay;
                }
            } else {
                value = parseInt(delay.substring(0, delay.length - 1));
            }
            switch (unit) {
                case TIME_UNIT.SECOND: {
                    second += value;
                    break;
                }
                case TIME_UNIT.MINUTE: {
                    minutes += value;
                    break;
                }
                case TIME_UNIT.HOURS: {
                    hour += value;
                    break;
                }
                case TIME_UNIT.DAY:
                default:
                    date += value;
                    break;
            }
        }
        // 
        if (clock) {
            let clockArray = clock.split(':');
            if (clockArray.length === 1) {
                if (Number.isInteger(clockArray[0])) {
                    hour = parseInt(clockArray[0]);
                } else {
                    let day = explain.getChineseDay(clockArray[0]);
                    if (Number.isInteger(day)) {
                        let currDay = now.getDay();
                        let diffDay = day - currDay;
                        diffDay = diffDay < 0 ? diffDay + 7 : diffDay;
                        date += diffDay;
                    }
                }
            } else {
                hour = parseInt(clockArray[0]);
                minutes = parseInt(clockArray[1]);
            }
        }

        let newDate = new Date(year, month, date, hour, minutes, second);
        let time = newDate.getTime();
        if (Number.isNaN(time)) {
        }
        return time;
    },
    getChineseDay(clock) {
        switch(clock) {
            case '':
            case '': return 1;
            case '':
            case '': return 2;
            case '':
            case '': return 3;
            case '':
            case '': return 4;
            case '':
            case '': return 5;
            case '':
            case '': return 6;
            case '':
            case '': return 7;
            default:
                return undefined;
        }
    },
    getSummaryInfo(collection) {
        let tempInfo = {
            [RENDER_STRING_KEY]: '',
            [RENDER_TAGS_KEY]: new Set(),
            [RENDER_TIME_KEY]: '',
        };
        collection.forEach(function (eachBlock) {
            let eachData = eachBlock.data;
            if (eachBlock.key === TOKEN_TYPE.TIME) {
                tempInfo[RENDER_TIME_KEY] = eachBlock.data.timeStamp;
            } else if(eachBlock.key === TOKEN_TYPE.TAG) {
                tempInfo[RENDER_TAGS_KEY].add(eachBlock.data.value);
            } else {
                tempInfo[RENDER_STRING_KEY] += eachData.value;
            }
        });
        return tempInfo;
    },
};

parser.test();

export default parser;